<template>
  <div class="wedding-editor" ref="editor" @touchstart="onTouchstart" @touchend="onTouchend">
    <header class="editor-header">
      <a href="javascript:;"></a>
      <a href="javascript:;" class="minimum"></a>
      <a href="javascript:;" class="maximum"></a>
    </header>
    <!-- Êó•Êúü -->
    <p class="code">
      Last login:
      <span>{{ startDate }}</span>on Earth
    </p>
    <!--‰ª£Á†ÅÁºñËæëÂå∫-->
    <pre>
      <code v-html="highlightedCode"></code>
    </pre>
    <!-- ÊâìÂºÄÈÇÄËØ∑ÂáΩ -->
    <div class="editor-open" v-if="(canStart || hasClosed) && !canOpen" @click="canOpen = true">
      <svg
        viewBox="0 0 24 24"
        width="18"
        height="18"
        stroke="currentColor"
        stroke-width="2"
        fill="none"
        stroke-linecap="round"
        stroke-linejoin="round"
        class="css-i6dzq1"
      >
        <rect x="3" y="3" width="18" height="18" rx="2" ry="2"></rect>
        <line x1="9" y1="3" x2="9" y2="21"></line>
      </svg>
    </div>
    <Executions :canExecute="canExecute" @onUpdating="scrollToBottom" @onFinish="canOpen = true"/>
    <invitation
      :canOpen="canOpen"
      @onClose="canOpen = false, hasClosed = true"
      @sendBarrage="onAfterSending"
    />
    <Barrage :wish="wish" :canStart="canStart"/>
    <audio
      autoplay
      src="https://huiyuanxitong.oss-cn-hangzhou.aliyuncs.com/temp/zmdqd.mp3"
      ref="music-bgm"
      style="display:none;"
    ></audio>
    <img
      v-if="!isPause"
      class="music-button animate__animated animate__heartBeat"
      src="https://huiyuanxitong.oss-cn-hangzhou.aliyuncs.com/temp/music-on.png"
      @click="musicPlay"
    >
    <img
      v-else
      class="music-button"
      src="https://huiyuanxitong.oss-cn-hangzhou.aliyuncs.com/temp/music-off.png"
      @click="musicPlay"
    >
  </div>
</template>

<script>
import wx from "weixin-js-sdk";
import axios from "axios";
import Prism from "prismjs";
import "prismjs/themes/prism-okaidia.css";
import "../utils/raf";
import data from "../mock/data";

import Executions from "./Executions";
import Invitation from "./Invitation";
import Barrage from "./Barrage";

export default {
  name: "Editor",
  components: { Executions, Invitation, Barrage },
  data() {
    return {
      startDate: "",
      code: data.code,
      currentCode: "",
      isCursorVisible: 1,
      canExecute: false,
      canOpen: false,
      wish: "",
      hasClosed: false,
      canStart: false,
      isPause: false,
      v1: 6,
      v2: 24
    };
  },
  created() {
    this.startDate = new Date().toDateString();
    this.progressivelyTyping();
  },
  updated() {
    this.scrollToBottom();
  },
  mounted() {
    const time = new Date().getTime();
    const str = this.randomString(10);
    axios
      .get(`http://www.fanwd.com/wxapp-fanwd-api/c/wx-js-ticket`, {
        params: {
          timestamp: time,
          nonceStr: str,
          url: location.href
        }
      })
      .then(res => {
        wx.config({
          appId: "wxf6267e4db38396dc", // ÂøÖÂ°´ÔºåÂÖ¨‰ºóÂè∑ÁöÑÂîØ‰∏ÄÊ†áËØÜ
          timestamp: time, // ÂøÖÂ°´ÔºåÁîüÊàêÁ≠æÂêçÁöÑÊó∂Èó¥Êà≥
          nonceStr: str, // ÂøÖÂ°´ÔºåÁîüÊàêÁ≠æÂêçÁöÑÈöèÊú∫‰∏≤
          signature: res.data.data.ticket, // ÂøÖÂ°´ÔºåÁ≠æÂêç
          jsApiList: ["updateAppMessageShareData", "updateTimelineShareData"] // ÂøÖÂ°´ÔºåÈúÄË¶Å‰ΩøÁî®ÁöÑJSÊé•Âè£ÂàóË°®
        });

        wx.ready(function() {
          // ÂàÜ‰∫´Â•ΩÂèã
          wx.updateAppMessageShareData({
            title: "Âë®Èí∞Ëá¥&Âº†ÈõØÁéâ,ÈÇÄËØ∑ÊÇ®~", // ÂàÜ‰∫´Ê†áÈ¢ò
            desc:
              "Êàë‰ª¨Â∞Ü‰∫é2020Âπ¥9Êúà20Êó•ÔºàÂë®Êó•ÔºâÂçàÂÆ¥12:18‰∏æË°åÂ©öÁ§ºÔºåËØöÈÇÄÊÇ®ÁöÑÂÖâ‰∏¥", // ÂàÜ‰∫´ÊèèËø∞
            link: location.href, // ÂàÜ‰∫´ÈìæÊé•
            imgUrl:
              "https://huiyuanxitong.oss-cn-hangzhou.aliyuncs.com/temp/share.png", // ÂàÜ‰∫´ÂõæÊ†á
            success: function() {},
            cancel: function() {}
          });
          // ÂàÜ‰∫´ÊúãÂèãÂúà
          wx.updateTimelineShareData({
            title: "Âë®Èí∞Ëá¥&Âº†ÈõØÁéâ,Êàë‰ª¨ÁªìÂ©öÂï¶~", // ÂàÜ‰∫´Ê†áÈ¢ò
            link: location.href, // ÂàÜ‰∫´ÈìæÊé•
            imgUrl:
              "https://huiyuanxitong.oss-cn-hangzhou.aliyuncs.com/temp/share.png", // ÂàÜ‰∫´ÂõæÊ†á
            success: function() {},
            cancel: function() {}
          });
        });
      });
    this.$refs["music-bgm"].play();
  },
  computed: {
    highlightedCode() {
      const code = Prism.highlight(
        this.currentCode,
        Prism.languages.javascript
      );
      const codeWithCursor = `${code}<span style="opacity:${
        this.isCursorVisible
      }"> ‚ñç</span>`;
      return codeWithCursor;
    }
  },
  methods: {
    scrollToBottom() {
      // ‰øùÊåÅÈ°µÈù¢‰∏ÄÁõ¥ÊªöÂà∞ÊúÄ‰∏ãÈù¢
      this.$refs.editor.scrollTop = 100000;
    },
    // ‰ª£Á†ÅËæìÂÖ•
    progressivelyTyping() {
      return new Promise(resolve => {
        let count = 0,
          typingCount = 0,
          typing;
        // ÂÜô‰ª£Á†ÅÊØè‰∏ÄÂ∏ßÁöÑÂáΩÊï∞
        let step = () => {
          let randomNumber = Math.round(Math.random() * this.v1);
          // Êë∏‰Ω†ÊâìÂ≠óÁöÑÈöèÊú∫ÈÄüÂ∫¶
          if (count % 2 === 0 && randomNumber % 4 === 0) {
            this.currentCode = this.code.substring(0, typingCount);
            typingCount++;
          }
          // Â§ßÁ∫¶ÊØè 24 Â∏ßÂÖâÊ†áÈó™Âä®‰∏ÄÊ¨°
          if (count % this.v2 === 0) {
            this.isCursorVisible = this.isCursorVisible === 0 ? 1 : 0;
          }
          count++;
          if (typingCount <= this.code.length) {
            typing = requestAnimationFrame(step);
          } else {
            resolve();
            this.canExecute = true;
            cancelAnimationFrame(typing);
          }
        };
        typing = requestAnimationFrame(step);
      });
    },
    // ÂèëÈÄÅÂºπÂπï‰πãÂêé
    onAfterSending(wish) {
      this.wish = wish;
      this.canOpen = false;
      setTimeout(() => {
        this.canStart = true;
      }, 800);
    },
    onTouchstart() {
      this.v1 = 1;
      this.v2 = 4;
    },
    onTouchend() {
      this.v1 = 6;
      this.v2 = 24;
    },
    randomString(len) {
      len = len || 32;
      /****ÈªòËÆ§ÂéªÊéâ‰∫ÜÂÆπÊòìÊ∑∑Ê∑ÜÁöÑÂ≠óÁ¨¶oOLl,9gq,Vv,Uu,I1****/
      var $chars = "ABCDEFGHJKMNPQRSTWXYZabcdefhijkmnprstwxyz2345678";
      var maxPos = $chars.length;
      var pwd = "";
      for (var i = 0; i < len; i++) {
        pwd += $chars.charAt(Math.floor(Math.random() * maxPos));
      }
      return pwd;
    },
    musicPlay() {
      if (this.$refs["music-bgm"].paused) {
        this.isPause = false;
        this.$refs["music-bgm"].play();
      } else {
        this.isPause = true;
        this.$refs["music-bgm"].pause();
      }
    }
  }
};
</script>

